.order__button {
    width: 515px;
    height: 60px;
    border: none;
    background: #59B722;
    border-radius: 100px;
    font-family: Montserrat;
    font-style: normal;
    font-weight: 600;
    font-size: 18px;
    line-height: 155.5%;
    color: #FFFFFF;
    cursor: pointer;
    margin: 2px auto 23px;
    outline: none;
}

.order__button:hover {
    background: #49A414;
    box-shadow: 4px 4px 8px rgba(37, 45, 36, 0.2);
    transition: all 0.3s ease-in;
}

@media screen and (max-width: 1250px) {
    .order__button {
        width: 415px;
    }
}

@media screen and (max-width: 990px) {
    .order__button {
        width: 362px;
        margin: 2px auto 8px;
    }
}